<template>
  <div>
    <div class="d-flex justify-content-between">
      <h4 class="mb-1">Market Exposure</h4>
      <div class="d-flex">
        <feather-icon icon="FilterIcon" class="cursor-pointer mr-1"/>
        <feather-icon icon="SettingsIcon" class="cursor-pointer mr-1" v-b-modal.modal-xs @click="table='market_exposure'"/>
        <feather-icon icon="FileTextIcon" class="cursor-pointer" @click="exportCSV"/>
      </div>
    </div>
    <div class="rounded my-shadow table-60-vh"> 
      <b-table-simple no-border-collapse sticky-header bordered small responsive striped class="rounded mb-0">
        <b-thead>
          <b-tr class="cursor-pointer">
            <b-th v-if="isTableShow('market_exposure', 'Name')" style="min-width: 150px">Name</b-th>
            <b-th v-if="isTableShow('market_exposure', 'Exposure Indicator')" style="min-width: 400px" >Exposure Indicator</b-th>
            <b-th v-if="isTableShow('market_exposure', 'Net Exposure')" @click="sortBy('NetExposure')">
              Net Exposure
              <feather-icon v-if="field_sort == 'NetExposure'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" />
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Risk')" @click="sortBy('Risk')">
              Risk
              <feather-icon v-if="field_sort == 'Risk'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" />
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Company Floating P/L')" @click="sortBy('CompanyFloatingPL')">
              <div class="d-flex">
                <div>Company<br/>Floating P/L</div>
                <div class="m-auto"><feather-icon v-if="field_sort == 'CompanyFloatingPL'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" /></div>
              </div>
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Net Position Lots')" @click="sortBy('NetPositionLots')">
               <div class="d-flex">
                <div>Net Position<br/>Lots</div>
                <div class="m-auto"><feather-icon v-if="field_sort == 'NetPositionLots'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" /></div>
              </div>
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Long Lots')" @click="sortBy('LongLots')">
              Long Lots
              <feather-icon v-if="field_sort == 'LongLots'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" />
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Short Lots')" @click="sortBy('ShortLots')">
              Short Lots
              <feather-icon v-if="field_sort == 'ShortLots'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" />  
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Closed P/L')" @click="sortBy('ClosedPL')">
              Closed P/L
              <feather-icon v-if="field_sort == 'ClosedPL'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" />  
            </b-th>
            <b-th v-if="isTableShow('market_exposure', 'Closed P/L per Lot')" @click="sortBy('ClosedPLPerLot')">
              <div class="d-flex">
                <div>Closed P/L<br/>per Lot</div>
                <div class="m-auto"><feather-icon v-if="field_sort == 'ClosedPLPerLot'" :icon="isDesc ? 'ChevronDownIcon' : 'ChevronUpIcon'" /></div>
              </div>
            </b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          
          <b-tr v-for="(item, index) in MECollapsed" :key="index" class="text-right">
            <b-td v-if="isTableShow('market_exposure', 'Name')" class="text-left" :style="{'padding-left': `${calcNamePadding(item.level)}px`}">
              <dot v-if="item.cannotCollapse" /> 
              <span v-else>
                <feather-icon icon="ChevronDownIcon" @click="appendMECollapse(item)" v-if="!MECollapseSet.includes(item)"/>
                <feather-icon icon="ChevronRightIcon" @click="removeMECollapse(item)" v-else/>
              </span>
              
              
              {{item.name}}
            </b-td>
            <b-td v-if="isTableShow('market_exposure', 'Exposure Indicator')" class="text-left">
              <div class=" position-relative" v-if="item.value.ExposureIndicator">
                <div :style="{height: '15px', width: `${item.value.ExposureIndicator[0]*200/maximumOfExposureIndicator}px`, maxWidth: '200px', top: '-7px', right: '50%'}" class="bg-success position-absolute"></div>
                <div :style="{height: '15px', width: `${Math.abs(item.value.ExposureIndicator[1]*200/maximumOfExposureIndicator)}px`, maxWidth: '200px', top: '-7px', left: '50%'}" class="bg-danger position-absolute"></div>		
              </div>
            </b-td>
            <b-td v-if="isTableShow('market_exposure', 'Net Exposure')" :class="{'text-danger': item.value.NetExposure < 0}">${{formatMoney(item.value.NetExposure, 0)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Risk')">
              <div :class="riskStyle(item.value.Risk)" style="width: 10px; height: 10px; border-radius: 5px"/>
            </b-td>
            <b-td v-if="isTableShow('market_exposure', 'Company Floating P/L')" :class="`${item.value.CompanyFloatingPL < 0 ? 'text-danger' : 'text-success'}` ">${{formatMoney(item.value.CompanyFloatingPL, 0)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Net Position Lots')" :class="{'text-danger': item.value.NetPositionLots < 0}">{{formatMoney(item.value.NetPositionLots)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Long Lots')" class="text-success text-right">{{formatMoney(item.value.LongLots)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Short Lots')" class="text-right">{{formatMoney(item.value.ShortLots)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Closed P/L')" :class="`${item.value.ClosedPL < 0 ? 'text-danger' : 'text-success'}` ">${{formatMoney(item.value.ClosedPL, 0)}}</b-td>
            <b-td v-if="isTableShow('market_exposure', 'Closed P/L per Lot')" :class="`${item.value.ClosedPLPerLot < 0 ? 'text-danger' : 'text-success'}` ">${{formatMoney(item.value.ClosedPLPerLot, 0)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 pr-1 d-flex justify-content-between">
        <div>
          <b-button size="sm" :class="`my-shadow ml-1 ${MERegionFilterSet.includes(1) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(1)"> Company </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${MERegionFilterSet.includes(2) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(2)"> Region </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${MERegionFilterSet.includes(3) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(3)"> Team </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${isGroupBySymbol ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="groupBy('Symbol')"> Symbol </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${isGroupByClient ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="groupBy('Client')"> Client </b-button>
          <b-button size="sm" class="my-shadow ml-1 button-active" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Account </b-button>
        </div>
        <div class="d-flex">
          <div class="mr-3">Symbols: {{market_exposure.filter(datum => datum.level == group_level).length }}</div>

          <div>Row: {{ MECollapsed.length }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'
import moment from 'moment'
import FeatherIcon from '@/@core/components/feather-icon/FeatherIcon.vue';
import Dot from "@/components/Dot"

export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    FeatherIcon, Dot
  },

  directives: {
    Ripple,
  },
  
  props: {
    riskStyle: Function,
    isTableShow: Function,
  },

  data(){
    const market_exposure_table_field = ['Name', 'Exposure Indicator', 'Net Exposure', 'Risk', 'Company Floating P/L', 'Net Position Lots', 'Long Lots', 'Short Lots', 'Closed P/L', 'Closed P/L per Lot']
    return {
      table_settings: {
        total_market_exposure: market_exposure_table_field, market_exposure: market_exposure_table_field,
      },

      market_exposure: [],
      table: 'market_exposure',
      
      group_level: 4,
      MERegionFilterSet: [],
      MECollapseSet: [],

      maximumOfExposureIndicator: 1,

      isGroupByClient: false,
      isGroupBySymbol: true,
      allClients: [],
      allSymbols: [],
      allAccounts: [],
      allAccountXSymbols: [],
      field_sort: 'NetExposure',
      isDesc: true,

      firstTimeSymbolCollapse: true
    }
  },

  computed: {
    MECollapsed(){
      if(this.firstTimeSymbolCollapse && this.dataAfterSorted.length > 1) {
        this.firstTimeSymbolCollapse = false
        this.dataAfterSorted.forEach(item => {if(item.level == 4){
          this.MECollapseSet.push(item)
        }})
      }

      return this.dataAfterSorted.filter(row => {
        if( row.level == 0) return false
        if( row.level >= this.group_level && this.formatMoney(row.value.NetExposure, 0) == 0) return false
        let show = true
        this.MECollapseSet.map(item => {
          show = show && !(row.path.startsWith(item.path) && item.path != row.path)
        })
        return show
      })
    },

    dataAfterSorted(){
      let desc_or_asc = this.isDesc ? -1 : 1
      let all_level = [...this.MERegionFilterSet, 4, 5, 6]
      all_level.sort()

      let result = []
      for(let i = 0; i < all_level.length; i++){
        let pos_or_neg = i == 0 ? desc_or_asc : -desc_or_asc 
        let data_of_current_level = this.dataAfterGroupBy.filter(datum => datum.level == all_level[i])
        data_of_current_level.sort((a, b) =>  pos_or_neg * (a.value[this.field_sort] - b.value[this.field_sort]))

        if(i == 0) result = result.concat(data_of_current_level)
        else {
          // Find correct index and insert the value
          data_of_current_level.forEach(current => {
            result.forEach((datum, index) => {
              if(datum.level == all_level[i-1] && current.path.startsWith(datum.path))
              result.splice(index + 1, 0, current)
            })
          })
        }
      }
      return result
    },

    dataAfterGroupBy(){
      let result = []
      let maxLevelOfFilter = 0
      if(this.MERegionFilterSet.length != 0) maxLevelOfFilter = Math.max(...this.MERegionFilterSet)

      let originData = maxLevelOfFilter == 0 ? [{level: 0, value: {}, path: ''}] : this.market_exposure

      if(!this.isGroupBySymbol && !this.isGroupByClient){
        originData.forEach(item => {
          if(item.level < maxLevelOfFilter) result.push(item) //Company or Region 

          if(item.level == maxLevelOfFilter){ // It's team
            result.push(item)
            this.allAccounts.forEach(account => { // Calc all account x symbol 
              let data = this.allAccountXSymbols.filter(sym => `${sym.properties.server}-${sym.properties.login}` == account && sym.path.startsWith(item.path))
              if(data.length){
                result.push({
                  level: 4,
                  cannotCollapse: 1,
                  name: account,
                  properties: {},
                  value: this.calcValueByData(data),
                  path: data[0].path + account.replace(/\s/g, '')
                })
              }
            })
          }
        })
      }

      if(this.isGroupBySymbol && !this.isGroupByClient){
        originData.forEach(item => {
          if(item.level < maxLevelOfFilter) result.push(item) //Company or Region 

          if(item.level == maxLevelOfFilter){ // It's team
            result.push(item)
            this.allSymbols.forEach(symbol => { // Calc all account x symbol for symbol row
              let data = this.allAccountXSymbols.filter(acc_sym => acc_sym.properties.symbol == symbol && acc_sym.path.startsWith(item.path))
              
              if(data.length){
                result.push({
                  level: 4,
                  name: symbol,
                  properties: {},
                  value: this.calcValueByData(data),
                  path: data[0].path + symbol
                })

                this.allAccounts.forEach(account => { //Then calc all account x symbol for account row
                  let data = this.allAccountXSymbols.filter(acc_sym => account == `${acc_sym.properties.server}-${acc_sym.properties.login}`&& acc_sym.properties.symbol == symbol && acc_sym.path.startsWith(item.path))
                  if(data.length)  result.push({ level: 5, name: account, properties: {}, value: this.calcValueByData(data), path: data[0].path + symbol + account, cannotCollapse: 1,})
                })
              }
            })
          }
        })
      }

      if(!this.isGroupBySymbol && this.isGroupByClient){
        originData.forEach(item => {
          if(item.level < maxLevelOfFilter) result.push(item) //Company or Region 

          if(item.level == maxLevelOfFilter){ // It's team
            result.push(item)
            this.allClients.forEach(client => { // Calc all account x symbol for symbol row
              let data = this.allAccountXSymbols.filter(acc_sym => acc_sym.properties.user == client && acc_sym.path.startsWith(item.path))
              
              if(data.length){
                result.push({
                  level: 4,
                  name: client,
                  properties: {},
                  value: this.calcValueByData(data),
                  path: data[0].path + client.replace(/\s/g, '')
                })

                this.allAccounts.forEach(account => { //Then calc all account x symbol for account row
                  let data = this.allAccountXSymbols.filter(acc_sym => account == `${acc_sym.properties.server}-${acc_sym.properties.login}`&& acc_sym.properties.user == client && acc_sym.path.startsWith(item.path))
                  if(data.length)  result.push({ level: 5, name: account, properties: {}, value: this.calcValueByData(data), path: data[0].path + client.replace(/\s/g, '') + account, cannotCollapse: 1,})
                })
              }
            })
          }
        })

      }

      if(this.isGroupBySymbol && this.isGroupByClient){
        originData.forEach(item => {
          if(item.level < maxLevelOfFilter) result.push(item) //Company or Region 

          if(item.level == maxLevelOfFilter){ // It's team
            result.push(item)
            this.allSymbols.forEach(symbol => { // Calc all account x symbol for symbol row
            let data = this.allAccountXSymbols.filter(acc_sym => acc_sym.properties.symbol == symbol && acc_sym.path.startsWith(item.path))
            
              if(data.length){
                result.push({
                  level: 4,
                  name: symbol,
                  properties: {},
                  value: this.calcValueByData(data),
                  path: data[0].path + symbol
                })
             
              this.allClients.forEach(client => { // Calc all account x symbol for client row
              let data = this.allAccountXSymbols.filter(acc_sym => acc_sym.properties.user == client && acc_sym.properties.symbol == symbol && acc_sym.path.startsWith(item.path))
                  
                  if(data.length){
                    result.push({ level: 5, name: client, properties: {}, value: this.calcValueByData(data), path: data[0].path + symbol + client.replace(/\s/g, '') })

                    this.allAccounts.forEach(account => { //Then calc all account x symbol for account row
                      let data = this.allAccountXSymbols.filter(acc_sym => account == `${acc_sym.properties.server}-${acc_sym.properties.login}`&& acc_sym.properties.symbol == symbol && acc_sym.properties.user == client && acc_sym.path.startsWith(item.path))
                      if(data.length)  result.push({ level: 6, name: account, properties: {}, value: this.calcValueByData(data), path: data[0].path + symbol + client.replace(/\s/g, '')  + account, cannotCollapse: 1,})
                    })
                  }
                })
              }
            })
          }
        })
      }
      
      return result
    }
  },

  methods: {
    formatMoney,
    moment,

    calcValueByData(data){
      let value = {
        ClosedPL: 0,
        ClosedPLPerLot: 0,
        CompanyFloatingPL: 0,
        ExposureIndicator: [0, 0],
        LongLots: 0,
        NetExposure: 0,
        NetPositionLots: 0,
        ShortLots: 0,
      }
      data.forEach(datum => {
        ['ClosedPL', 'ClosedPLPerLot', 'CompanyFloatingPL', 'LongLots', 'NetExposure', 'NetPositionLots', 'ShortLots'].forEach(label => {
          if(datum.value[label]) value[label] += parseFloat(datum.value[label])
        }) 
        if(datum.value.ExposureIndicator) {
          value.ExposureIndicator[0] += datum.value.ExposureIndicator[0]
          value.ExposureIndicator[1] += datum.value.ExposureIndicator[1]
        }
      })
      value.Risk = value.NetExposure > 500000 ? 3 : value.NetExposure > 100000 ? 2 : 1
      return value
    },

    groupBy(label){
      this.MECollapseSet = []
      this[`isGroupBy${label}`] = !this[`isGroupBy${label}`];
    },

    sortBy(field){
      this.field_sort = field
      this.isDesc = !this.isDesc
    },

    exportCSV(){
      const link = document.createElement("a");
      data = [
          'Name, Exposure Indicator, Net Exposure, Risk, Company Floating P/L, Net Position Lots, Long Lots, Short Lots, Closed P/L, Closed P/L per Lot',
          ...this.market_exposure.map(item => {
            return [item.name, item.value.ExposureIndicator ? item.value.ExposureIndicator.join('/') : '', item.value.NetExposure, item.value.Risk, item.value.CompanyFloatingPL, item.value.NetPositionLots, item.value.LongLots, item.value.ShortLots, item.value.ClosedPL, item.value.ClosedPLPerLot].join(',')
          })
        ].join("\n")

      let blob = new Blob([data], { type: 'text/csv;charset=utf-8;' });
      let url = URL.createObjectURL(blob);
      link.setAttribute("href", url);
      link.setAttribute("download", `${metric_name}${moment().format('x')}.csv`);
      link.click();
    },

    filterMarketExposureRegion(level){
      if(this.MERegionFilterSet.includes(level))
        this.MERegionFilterSet = this.MERegionFilterSet.filter(item => item != level)
      else this.MERegionFilterSet.push(level)

      this.fetch_market_exposure()
    },

    appendMECollapse(item){
      this.MECollapseSet.push(item)
    },
    
    removeMECollapse(item){
      this.MECollapseSet = this.MECollapseSet.filter(i => i != item) 
    },

    calcNamePadding(level){
      let a = [...this.MERegionFilterSet, 4,5,6]
      a.sort()
      return 25 * a.indexOf(level)
    },

    fetch_market_exposure(){
      this.MECollapseSet = []
      let url = `${process.env.VUE_APP_API_URL}metric/MarketExposure/data?group=true`
      for(let i = 1; i < this.group_level; i ++){
        if(this.MERegionFilterSet.includes(i)) url += `&level${i}=1`
      }
      axios.get(url).then(res => {
        this.market_exposure = res.data
        
        res.data.map(item => {
          if( item.properties.user && !this.allClients.includes(item.properties.user)) this.allClients.push(item.properties.user)
          if(item.level == this.group_level){
            let symbol = item.name.split('_')[0]
            if( !this.allSymbols.includes(symbol)) this.allSymbols.push(symbol)

            let account = `${item.properties.server}-${item.name.split('_')[1]}`
            if( !this.allAccounts.includes(account)) this.allAccounts.push(account)

            if( !this.allAccountXSymbols.includes(item)) this.allAccountXSymbols.push(item)

          }
        })


        let EI0 = res.data.map(item => item.value.ExposureIndicator ? item.value.ExposureIndicator[0] : 0)
        let EI1 = res.data.map(item => item.value.ExposureIndicator ? item.value.ExposureIndicator[1] : 1)
        this.maximumOfExposureIndicator = Math.max(Math.max(...EI0), Math.abs(Math.min(...EI1)))
      })
    },

  },

  channels: {
    MetricDataUpdateChannel: {
      connected() {
         console.log('Connected to the chat channel');
      },
      rejected() {
         console.log('Rejected to the chat channel');
      },
      received(data) {
        console.log('Received ', data.payload.context.metric_definition.name)
        
        if(data.payload.context.metric_definition && data.payload.context.metric_definition.name){

          


          let metric_datum_id = data.payload.context.id
          let value = data.payload.context.value
          
          if(['MarketExposure', 'MarketExposure_Region'].includes(data.payload.context.metric_definition.name)){
            
            this.market_exposure = this.market_exposure.map(datum => {
              if(datum.id != metric_datum_id) return datum;
              return {...datum, value: value}
            })

            this.$store.commit('app/SET_DATA_LIVE_TIME', moment().format(' HH:mm:ss - DD/MM/YYYY'))
            
            console.log('Changed ', data.payload)
          }
        }
      },
      disconnected() {
        console.log('Disconnected to the chat channel');
      }
    }
  },

  mounted() {
    this.fetch_market_exposure()

  },
}
</script>