<template>
  <div>
    <div class="d-flex justify-content-between">
      <h4 class="mb-1">Platform Open Positions</h4>
      <div class="d-flex">
        <feather-icon icon="FilterIcon" class="cursor-pointer mr-1"/>
        <feather-icon icon="SettingsIcon" class="cursor-pointer mr-1" v-b-modal.modal-xs @click="table='platform_deal'"/>
        <feather-icon icon="FileTextIcon" class="cursor-pointer" @click="exportCSV"/>
      </div>
    </div>

    <div class="rounded my-shadow table-50-vh"> 
      <b-table-simple no-border-collapse sticky-header bordered small responsive striped  class="rounded mb-0 market_exposure_table">
        <b-thead>
          <b-tr>
            <b-th v-if="isTableShow('platform_deal', 'Name')" style="min-width: 150px">Name</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Account')" style="min-width: 250px">Account</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Time')" style="min-width: 200px">Time</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Type')">Type</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Symbol')">Symbol</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Volume')">Volume</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Reason')">Reason</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Swap')">Swap</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Profit')">Profit</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Currency')">Currency</b-th>
            <b-th v-if="isTableShow('platform_deal', 'Profit (USD)')">Profit<br/>(USD)</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          
          <b-tr v-for="(item, index) in dataAfterCollapse" :key="index" class="text-right">
            <b-td v-if="isTableShow('platform_deal', 'Name')" class="text-left" :style="platformDealPaddingLeft(item.level)">
              <span v-if="item.level < group_level">
                <feather-icon icon="ChevronDownIcon" @click="appendPDCollapse(item)" v-if="!PDCollapseSet.includes(item)"/>
                <feather-icon icon="ChevronRightIcon" @click="removePDCollapse(item)" v-else/>
              </span>
              
              <dot v-else/>
              {{item.id}}
              
              </b-td>
            <b-td v-if="isTableShow('platform_deal', 'Account')" class="text-left">{{item.server &&  item.login ? `${item.server}-${item.login}` : ''}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Time')" class="text-left">{{item.timestamp ? moment(item.timestamp).format('YYYY.MM.DD HH:mm:ss') : ''}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Type')">{{action[item.action]}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Symbol')">{{item.symbol}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Volume')">{{item.volume}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Reason')">{{item.reason}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Swap')">{{item.storage ? formatMoney(item.storage): ''}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Profit')" :class="item.profit >= 0 ? 'text-success' : 'text-danger'">{{item.profit ? formatMoney(item.profit) : ''}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Currency')">{{item.currency}}</b-td>
            <b-td v-if="isTableShow('platform_deal', 'Profit (USD)')" :class="item.profit >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.profit_usd)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 d-flex justify-content-between">
        <div>
          <b-button size="sm" :class="`my-shadow ml-1 ${PDRegionFilterSet.includes(1) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterPlatformDealRegion(1)"> Company </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${PDRegionFilterSet.includes(2) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterPlatformDealRegion(2)"> Region </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${PDRegionFilterSet.includes(3) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterPlatformDealRegion(3)"> Team </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Client </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Account </b-button>
          <b-button size="sm" class="my-shadow ml-1 button-active" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Open Position </b-button>
        </div>
        <div class="mr-2 my-auto">
          Open Positions: {{mt_deals.filter(item => item.login).length}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'
import moment from 'moment'
import Dot from "@/components/Dot"

export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    Dot
  },

  directives: {
    Ripple,
  },

  props: {
    isTableShow: Function,
  },
  data(){
    const platform_deal_table_field = ['Name', 'Account', 'Time', 'Type', 'Symbol', 'Volume', 'Reason', 'Swap', 'Profit', 'Currency', 'Profit (USD)']
    return {
      mt_deals: [],
      table_settings: {
        total_platform_deal: platform_deal_table_field, platform_deal: platform_deal_table_field,
      },
      
      action: ['Buy', 'Sell', 'BuyLimit', 'SellLimit', 'BuyStop', 'SellStop', ' Balance', 'Credit'],

      group_level: 4,

      PDRegionFilterSet: [1,2,3],
      PDCollapseSet: []
    }
  },

   computed: {
    dataAfterCollapse(){
      return this.mt_deals.filter(row => {
        let show = true
        this.PDCollapseSet.map(item => {
          show = show && !(row.path.startsWith(item.path) && item.path != row.path)
        })
        return show
      })
    },
  },

  methods: {
    formatMoney, moment,

    exportCSV(){
      const link = document.createElement("a");
      data = [
          'Name, Account, Time, Type, Symbol, Volume, Reason, Swap, Profit, Currency, Profit (USD)',
          ...this.mt_deals.map(item => {
            return [item.id, item.login, moment(item.timestamp).format('YYYY.MM.DD HH:mm:ss'), this.action[item.action], item.symbol, item.volume, item.reason, item.storage, item.profit, 'USD', item.profit].join(',')
          })
        ].join("\n")

      let blob = new Blob([data], { type: 'text/csv;charset=utf-8;' });
      let url = URL.createObjectURL(blob);
      link.setAttribute("href", url);
      link.setAttribute("download", `${metric_name}${moment().format('x')}.csv`);
      link.click();
    },

    platformDealPaddingLeft(level){
      let result = (this.group_level - 1) * 20
      if(level) result = (level - 1) * 20
      return  {'padding-left': `${result}px!important`}
    },

    sortByHasValue(data){
      return data.sort((a, b) => Object.keys( b.value).length - Object.keys(a.value).length)
    },

    filterPlatformDealRegion(level){
      if(this.PDRegionFilterSet.includes(level))
        this.PDRegionFilterSet = this.PDRegionFilterSet.filter(item => item != level)
      else this.PDRegionFilterSet.push(level)

      this.fetch_mt_deals()
    },

    appendPDCollapse(item){
      this.PDCollapseSet.push(item)
    },
    
    removePDCollapse(item){
      this.PDCollapseSet = this.PDCollapseSet.filter(i => i != item) 
    },

    fetch_mt_deals(){
      let url = `${process.env.VUE_APP_API_URL}mt_open_trades?group=true`
      for(let i = 1; i < this.group_level; i ++){
        if(this.PDRegionFilterSet.includes(i)) url += `&level${i}=1`
      }
      axios.get(url).then(res => {
        this.mt_deals = res.data
      })
    }
  },

  mounted(){
    this.fetch_mt_deals()
  }
}
</script>