<template>
  <div class="custom">
    <!-- Table Settings Modal -->
    <b-modal id="modal-xs" cancel-variant="secondary" ok-only ok-title="Confirm" centered size="xs" title="Table Settings" @ok="saveTableSettings">
      <div v-for="(item, index) in table_settings[`total_${table}`]" :key="index">
         <b-form-checkbox :value="item" v-model="table_settings[table]" class="custom-control-primary my-1">
          {{item}}
        </b-form-checkbox>
      </div>
    </b-modal>

    <b-tabs vertical nav-wrapper-class="nav-vertical">
      <b-tab active>
        <template #title>
          <feather-icon icon="PieChartIcon"/>
        </template>
        <!-- Market Exposure table -->
        <div class="mb-5" style="max-width: calc(100vw - 148px)">
          <market-exposure-table
            :isTableShow="isTableShow"
            :riskStyle="riskStyle"
          />
        </div>

        <div class="row">
          <!-- Platform deal table -->
          <div class="col-8">
            <platform-deal-table  :isTableShow="isTableShow"/>
          </div>

          <!-- Trading Account Exposure table -->
          <div class="col-4">
            <trading-account-table 
              :isTableShow="isTableShow"
              :riskStyle="riskStyle"
            />
          </div>
        </div>
      </b-tab>
      
      <b-tab @click="$router.push('/b-book/user-stats')">
        <template #title>
          <feather-icon icon="UsersIcon"/>
        </template>
      </b-tab>
      
      <b-tab @click="$router.push('/b-book/company-p-l')">
        <template #title>
          <feather-icon icon="FilterIcon"/>
        </template>
      </b-tab>

      <b-tab @click="$router.push('/b-book/deep-dive')">
        <template #title>
          <feather-icon icon="BoxIcon"/>
        </template>
      </b-tab>

    </b-tabs>
  </div>
</template>
<script>
import { BTabs, BTab, BModal, VBModal, BFormCheckbox } from 'bootstrap-vue'

import MarketExposureTable from './MarketExposureTable'
import PlatformDealTable from './PlatformDealTable'
import TradingAccountTable from './TradingAccountTable'

export default {
  components: {
    BTabs,
    BTab,
    BModal,
    BFormCheckbox,
    MarketExposureTable,
    PlatformDealTable,
    TradingAccountTable
  },
  directives: {
    'b-modal': VBModal,
  },
  data(){
    const platform_deal_table_field = ['Name', 'Account', 'Time', 'Type', 'Symbol', 'Volume', 'Reason', 'Swap', 'Profit', 'Currency', 'Profit (USD)']
    const market_exposure_table_field = ['Name', 'Exposure Indicator', 'Net Exposure', 'Risk', 'Company Floating P/L', 'Net Position Lots', 'Long Lots', 'Short Lots', 'Closed P/L', 'Closed P/L per Lot']
    return {
      table_settings: {
        total_market_exposure: market_exposure_table_field, market_exposure: market_exposure_table_field,
        total_platform_deal: platform_deal_table_field, platform_deal: platform_deal_table_field,
      },
      table: 'market_exposure',
    }
  },

  methods: {
    isTableShow(table, field){
      return this.table_settings[table].includes(field)
    },
    
    saveTableSettings(){
      localStorage.setItem(`table_settings_${this.table}`, JSON.stringify(this.table_settings[this.table]))
    },
    
    riskStyle(int) {
      let x = ['bg-success mx-auto', 'bg-warning mx-auto', 'bg-danger mx-auto']
      return x[int - 1]
    },
  },

  
  mounted() {
    let settings_market_exposure = JSON.parse(localStorage.getItem('table_settings_market_exposure'))
    if(settings_market_exposure) this.table_settings.market_exposure = settings_market_exposure

    let settings_platform_deal = JSON.parse(localStorage.getItem('table_settings_platform_deal'))
    if(settings_platform_deal) this.table_settings.platform_deal = settings_platform_deal
  }
};
</script>