<template>
  <div>
    <div class="d-flex justify-content-between">
      <h4 class="mb-1">Trading Account Exposure</h4>
      <div class="d-flex">
        <feather-icon icon="FilterIcon" class="cursor-pointer mr-1"/>
        <feather-icon icon="FileTextIcon" class="cursor-pointer" @click="exportCSV"/>
      </div>
    </div>

    <div class="rounded my-shadow table-50-vh"> 
      <b-table-simple no-border-collapse small sticky-header bordered responsive striped  class="rounded mb-0 market_exposure_table">
        <b-thead>
          <b-tr>
            <b-th style="min-width: 200px">Name</b-th>
            <b-th>Symbol</b-th>
            <b-th style="min-width: 120px">Net Vol</b-th>
            <b-th>Exposure</b-th>
            <b-th>Risk</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          
          <b-tr v-for="(item, index) in dataAfterCollapse" :key="index" class="text-right">
            <b-td class="text-left" :style="{'padding-left': `${calcNamePadding(item.level)}px`}">
              <dot v-if="item.cannotCollapse"/>
              <span v-else>
                <feather-icon icon="ChevronDownIcon" @click="appendTACollapse(item)" v-if="!TACollapseSet.includes(item)"/>
                <feather-icon icon="ChevronRightIcon" @click="removeTACollapse(item)" v-else/>
              </span> &ensp;

              <span v-if="item.properties.server && item.properties.login">{{item.properties.server}}-{{item.properties.login}}</span>
              <span v-else>{{item.name}}</span>
            </b-td>
            <b-td>{{item.properties.symbol}}</b-td>
            <b-td :class="item.value.NetPositionLots >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.NetPositionLots, 2)}}</b-td>
            <b-td>${{formatMoney(item.value.NetExposure, 0)}}</b-td>
            <b-td>
              <div :class="riskStyle(item.value.Risk)" style="width: 10px; height: 10px; border-radius: 5px"/>
            </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 pr-1 d-flex justify-content-between">
        <div>
          <b-button size="sm" :class="`my-shadow ml-1 ${TARegionFilterSet.includes(1) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(1)"> Company </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${TARegionFilterSet.includes(2) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(2)"> Region </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${TARegionFilterSet.includes(3) ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="filterMarketExposureRegion(3)"> Team </b-button>
          <b-button size="sm" :class="`my-shadow ml-1 ${isGroupByClient ? 'button-active': ''}`" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill @click="isGroupByClient = !isGroupByClient"> Client </b-button>
          <b-button size="sm" class="my-shadow ml-1 button-active" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Account </b-button>
        </div>

        <div class="my-auto">
          Items: {{dataAfterCollapse.length}}
        </div>
        
      </div>
    </div>
  </div>
</template>
<script>

import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'
import moment from 'moment'
import Dot from "@/components/Dot"

export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    Dot
  },

  directives: {
    Ripple,
  },

  props: {
    riskStyle: Function,
  },

  data(){
    return {
      trading_accounts: [],
      
      group_level: 4,
      TARegionFilterSet: [1,2,3],
      TACollapseSet: [],
      allClients: [],
      allAccountXSymbols: [],
      isGroupByClient: false
    }
  },

  computed: {
    dataAfterCollapse(){
      return this.dataAfterSorted.filter(row => {
        if( row.level == 0) return false
        if( row.level >= this.group_level && this.formatMoney(row.value.NetExposure, 0) == 0) return false
        let show = true
        this.TACollapseSet.map(item => {
          show = show && !(row.path.startsWith(item.path) && item.path != row.path)
        })
        return show
      })
    },

    dataAfterSorted(){
      return this.dataAfferGroupBy
    },

    dataAfferGroupBy(){
      if(this.isGroupByClient){
        let result =  []
        let maxLevelOfFilter = 0
        if(this.TARegionFilterSet.length != 0) maxLevelOfFilter = Math.max(...this.TARegionFilterSet)
        let originData = maxLevelOfFilter == 0 ? [{level: 0, value: {}, path: ''}] : this.trading_accounts

        originData.forEach(item => {
          if(item.level < maxLevelOfFilter) result.push(item) //Company or Region 

          if(item.level == maxLevelOfFilter){ // It's team
            result.push(item)
            this.allClients.forEach(client => { // Calc all account x symbol for symbol client row
              let data = this.allAccountXSymbols.filter(acc_sym => acc_sym.properties.user == client && acc_sym.path.startsWith(item.path))
              
              if(data.length){
                result.push({level: 4, name: client, properties: {}, value: this.calcValueByData(data), path: data[0].path + client}) 
                result = result.concat(data.map(i => { return {...i, cannotCollapse: 1, level: 5, path: data[0].path + client + data.name}}))
              }
            })
          }
        })
        return result
      }
      return this.trading_accounts.map(item =>  {
        if(item.level == this.group_level) return {...item, cannotCollapse: 1}
        return item
      })
    },
  
  },

  methods: {
    formatMoney, moment,

    filterMarketExposureRegion(level){
      if(this.TARegionFilterSet.includes(level))
        this.TARegionFilterSet = this.TARegionFilterSet.filter(item => item != level)
      else this.TARegionFilterSet.push(level)

      this.fetch_market_exposure()
    },

    calcNamePadding(level){
      let a = [...this.TARegionFilterSet, 4,5]
      a.sort()
      return 25 * a.indexOf(level)
    },

    appendTACollapse(item){
      this.TACollapseSet.push(item)
    },
    
    removeTACollapse(item){
      this.TACollapseSet = this.TACollapseSet.filter(i => i != item) 
    },


    calcValueByData(data){
      let value = { NetExposure: 0, NetPositionLots: 0 }
      data.forEach(datum => {
        ['NetExposure', 'NetPositionLots'].forEach(label => {
          if(datum.value[label]) value[label] += parseFloat(datum.value[label])
        }) 
      })
      value.Risk = value.NetExposure > 500000 ? 3 : value.NetExposure > 100000 ? 2 : 1
      return value
    },

    exportCSV(){
      const link = document.createElement("a");
      data = [
          'Name, Account, Time, Type, Symbol, Volume, Reason, Swap, Profit, Currency, Profit (USD)',
          ...this.mt_deals.map(item => {
            return [item.id, item.login, moment(item.timestamp).format('YYYY.MM.DD HH:mm:ss'), this.action[item.action], item.symbol, item.volume, item.reason, item.storage, item.profit, 'USD', item.profit].join(',')
          })
        ].join("\n")

      let blob = new Blob([data], { type: 'text/csv;charset=utf-8;' });
      let url = URL.createObjectURL(blob);
      link.setAttribute("href", url);
      link.setAttribute("download", `${metric_name}${moment().format('x')}.csv`);
      link.click();
    },

    fetch_market_exposure(){
      this.TACollapseSet = []
      let url = `${process.env.VUE_APP_API_URL}metric/MarketExposure/data?group=true`
      for(let i = 1; i < this.group_level; i ++){
        if(this.TARegionFilterSet.includes(i)) url += `&level${i}=1`
      }
      axios.get(url).then(res => {
        this.trading_accounts = res.data
        res.data.map(item => {
          if( item.properties.user && !this.allClients.includes(item.properties.user)) this.allClients.push(item.properties.user)
          if( item.level == this.group_level && !this.allAccountXSymbols.includes(item)) this.allAccountXSymbols.push(item)
        })
        
      })
    },
  },

  mounted() {
    this.fetch_market_exposure()
  },
}
</script>